import React from 'react';
import { MdArrowDownward, MdArrowUpward } from 'react-icons/md';
import { useHistory } from 'react-router';
import '../styles/HomeMobile.css';

const HomeMobile = ({ customers }) => {

    let history = useHistory();

    const calcBalance = (customer) => {
        return (customer.facture + customer.paiement)
    }

    const showCustomer = (customer) => {
        history.push('/customers/' + customer.name);
    }


    return (
        <div>
            <header className='header-HomeMobile'>
                <div>
                    <h3>Bienvenue</h3>
                    <h1>Geffrey SAID</h1>
                </div>
                <div className="profilImg">
                </div>
            </header>
            <div className="cardCustomers">
                {customers !== undefined &&
                    <div className='card-list'>
                        {
                            customers.map(((customer) => {
                                return (
                                    <div className="customer-card" onClick={() => showCustomer(customer)}>
                                        <h4>{customer.name} <span>VISA</span></h4>
                                        <p>{calcBalance(customer)},00 €</p>
                                    </div>
                                )
                            }))
                        }
                    </div>
                }
            </div>

            <div className="transaction-mobile">
                <div className="recap">
                    <div className="recap-card">
                        <p className='recap-arrow recap-arrow-up'> <MdArrowUpward size={25} /> </p>
                        <p> Factures <br /> <span>1000,00 €</span> </p>
                    </div>
                    <div className="recap-card">
                        <p className='recap-arrow recap-arrow-down'> <MdArrowDownward size={25} /> </p>
                        <p> Paiements <br /> <span>1000,00 €</span> </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default HomeMobile;